import Cookies from "js-cookie";
import { fromLonLat } from "ol/proj";
import Polygon from "ol/geom/Polygon";

export default {
  listeners: [],
  getOnce(l) {
    this.listeners.push({ method: l, recurrent: false });
    this.call();
  },
  get(l) {
    this.listeners.push({ method: l, recurrent: true });
    this.call();
  },
  current: null,
  posToPoint(p) {
    return fromLonLat(p);
  },
  contained(point, coords) {
    return new Polygon(coords).intersectsCoordinate(point);
  },
  update() {
    let cookie = Cookies.getJSON("currentLocation");
    if (cookie) {
      return this.set(cookie);
    }
    this.poll();
  },
  set(l) {
    this.current = l;
    Cookies.set("currentLocation", l);
    this.call();
  },
  refresh(callback) {
    this.set(null);
    callback && this.getOnce(callback);
  },
  polling: false,
  poll() {
    if (this.polling) {
      return;
    }
    this.polling = true;
    navigator.geolocation.watchPosition(
      v => this.handleNavPos(v),
      error => console.error(`Error fetching position: ${error.message}`),
      {
        enableHighAccuracy: true
      }
    );
  },
  handleNavPos(pos) {
    const coords = [pos.coords.longitude, pos.coords.latitude];
    const accuracy = pos.coords.accuracy;
    this.set({
      coords,
      accuracy
    });
  },
  call() {
    if (this.current === null) {
      return this.update();
    }

    let l = this.listeners;
    this.listeners = [];

    while (l.length) {
      const listener = l.pop();
      listener.method(this.current);
      if (listener.recurrent) {
        this.listeners.push(listener);
      }
    }
  }
};
