<template>
  <div
    :class="{ load: location === null, 'mb-5': location !== null }"
    class="mb-5"
  >
    <condition-set-view
      :conditionSet="conditionSet"
      @refresh="refresh"
      v-if="location !== null"
    />
    <location-loader v-if="location === null" />
  </div>
</template>

<script>
import ConditionSetView from "@/components/ConditionSetView.vue";
import LocationLoader from "@/components/LocationLoader.vue";
import { faUserFriends, faMapPin } from "@fortawesome/free-solid-svg-icons";
import LocationService from "@/services/location.js";

const axios = require("axios");
export default {
  components: { ConditionSetView, LocationLoader },
  data() {
    return {
      faUserFriends,
      faMapPin,
      conditionSet: null,
      location: null,
      dataPoints: []
    };
  },
  mounted() {
    axios.get("/data.json").then(response => {
      this.setDataPoints(response.data.dataPoints);
    });
    LocationService.getOnce(l => {
      this.setLocation(l);
    });
  },
  methods: {
    setDataPoints(v) {
      this.dataPoints = v;
      this.getConditionSet();
    },
    setLocation(v) {
      this.location = v;
      this.getConditionSet();
    },
    getConditionSet() {
      if (this.location === null) {
        this.conditionSet = null;
        return;
      }
      const pos = this.location.coords;
      const point = LocationService.posToPoint(pos);

      this.conditionSet = this.dataPoints.find(p =>
        LocationService.contained(point, p.poly)
      );
    },
    refresh() {
      this.setLocation(null);
      LocationService.refresh(l => {
        this.setLocation(l);
      });
    }
  }
};
</script>

