<template>
  <div
    class="host"
    :class="{ clickable: more !== '' }"
    :style="{ backgroundColor: colorName }"
    @click="toggle"
  >
    <div class="d-flex big">
      <div>
        <font-awesome-icon class="icon" :icon="iconName" fixed-width />
      </div>
      <div v-if="link === ''" class="expand">
        {{ text
        }}<font-awesome-icon
          v-if="addMapButton"
          :icon="faMapMarkedAlt"
          class="dim"
        />
      </div>
      <a
        v-if="link !== ''"
        href="javascript:void(0)"
        class="expand"
        @click="openLink"
        >{{ text }}</a
      >
      <div v-if="more !== ''">
        <font-awesome-icon class="arrow" :icon="faCaretDown" fixed-width />
      </div>
    </div>

    <div ref="accordion" class="accordion-collapse collapse">
      <div class="accordion-body">
        {{ more }}
      </div>
    </div>
  </div>
</template>

<script>
import { faCaretDown, faMapMarkedAlt } from "@fortawesome/free-solid-svg-icons";
import globals from "@/components/globals.js";
const $ = window.$;

export default {
  data() {
    return {
      faCaretDown,
      faMapMarkedAlt,
      shown: false
    };
  },
  props: {
    icon: {
      type: Number,
      default: 0
    },
    addMapButton: {
      type: Number,
      default: 0
    },
    color: {
      type: Number,
      default: 0
    },
    text: {
      type: String
    },
    link: {
      type: String,
      default: ""
    },
    more: {
      type: String,
      default: ""
    }
  },
  computed: {
    iconName() {
      return globals.icons[this.icon];
    },
    colorName() {
      return globals.colors[this.color];
    }
  },
  methods: {
    toggle() {
      $(this.$refs.accordion).collapse("toggle");
    },
    openLink() {
      window.open(this.link);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.host {
  width: 100%;
  text-align: left;
  margin: 0.2em;
  padding: 0.5em;
  border-radius: 0.2em;
  &.clickable {
    cursor: pointer;
  }
}
.big {
  font-size: 1.4em;
  .icon {
    font-size: 1.4em;
    margin-right: 0.5em;
  }
}
.dim {
  opacity: 0.8;
}
</style>
